import { styled } from "@mui/material/styles";
import { Box, Typography, Avatar } from "@mui/material";
import moment from "moment";

const PREFIX = "CommentItem";

const classes = {
  avatar: `${PREFIX}-avatar`,
  main: `${PREFIX}-main`,
  infoItem: `${PREFIX}-infoItem`,
  commentText: `${PREFIX}-commentText`,
};

const StyledBox = styled(Box)(({ theme }) => ({
  [`& .${classes.avatar}`]: {
    marginRight: theme.spacing(2),
  },

  [`& .${classes.main}`]: {
    flex: 1,
  },

  [`& .${classes.infoItem}`]: {
    "& + &": {
      marginLeft: theme.spacing(2),
    },
  },

  [`& .${classes.commentText}`]: {
    marginTop: theme.spacing(1),
  },
}));

export interface CommentItemProps {
  id?: number | string;

  create_at?: Date;

  content: string;

  author?: {
    avatar?: string;
    nickname?: string | null;
    description?: string | null;
  };
}

function CommentItem(props: CommentItemProps) {
  return (
    <StyledBox>
      <Box display="flex">
        <Avatar className={classes.avatar} src={props.author?.avatar}></Avatar>
        <div className={classes.main}>
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
          >
            <Typography>{props.author?.nickname}</Typography>
            <Typography variant="caption" color="GrayText">
              {moment(props.create_at).format("YYYY-MM-DD HH:mm")}
            </Typography>
          </Box>
          <Box display="flex">
            <Typography variant="caption" color={"GrayText"}>
              {props.author?.description}
            </Typography>
          </Box>
        </div>
      </Box>
      <Typography variant="body2" className={classes.commentText}>
        {props.content}
      </Typography>
    </StyledBox>
  );
}

export default CommentItem;
